.articles {
  background-color: #fafbfc;
  padding-bottom: 100px;
}

.card {
  position: relative;
  margin: 20px 0px 20px 0px;
  box-shadow: rgba(25, 17, 34, 0.5) 0px 5px 10px;
}

@media (min-width: map-get($grid-breakpoints, md)) {
  .card {
    flex: 1;
    margin: 20px;
    box-shadow: rgba(25, 17, 34, 0.5) 0px 5px 10px;
    transition: all 0.3s;

    &:first-child {
      margin-left: 0px;
    }

    &:last-child {
      margin-right: 0px;
    }

    &:hover {
      //transform: scale(1.03,1.03);
      box-shadow: rgba(25, 17, 34, 0.6) 0px 10px 20px;
    }
  }
}

.article-overlay {
  position: relative;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.7)
}

.card-inner {
  color: white;
  padding: 15px 20px 15px 20px;
}

.bg-card {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.category {
  font-size: 18px;
  border-radius: 0px;
  padding: 10px;
  margin-right: 15px;
  margin-bottom: 10px;
  display: inline-block;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  transition: all 0.3s;

}

// category mixin
@mixin category-color($bg-color, $text-color: $light) {
  background-color: $bg-color;
  color: $text-color;
  &:hover {
    color: $text-color;
  }
}

.category-javascript {
  @include category-color($javascript, $gray-800)
}

.category-react {
  @include category-color($gray-800, $react)
}

.category-nodejs {
  @include category-color($nodejs)
}

.category-git {
  @include category-color($git)
}

.category-laravel {
  @include category-color($laravel)
}

.category-php {
  @include category-color($php)
}

.category-html {
  @include category-color($html)
}

.category-css {
  @include category-color($css)
}

.category-devops {
  @include category-color($theme-secondary-color)
}

.category-general {
  @include category-color($general)
}

// Single article
.article-title {
  font-size: 60px;
}

.author {
    color: $theme-secondary-color;

  }

@media (max-width: map-get($grid-breakpoints, md)) {
  .article-title {
    font-size: 40px; 
  }
}

.content-title {
  color: $gray-800;
  margin-top: 65px;
  display: block;
  &:hover {
    color: $theme-main-color;
  }
}

.article-body {
  p {
    line-height: 1.75;
    margin-top: 25px;
    font-size: 18px;
  }

  h3, h4, h5 {
    margin-top: 40px;
  }

  a {
    border-bottom: 4px solid $theme-main-color;
  }

  img {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .notice {
    background: $gray-400;
    padding-right: 5px;
    padding-left: 5px;
  }

  .hl-info {
    display: block;
    background-color: $gray-900;
    color: white;
    padding: 10px;
    border-bottom: 2px solid white;
    font-size: 85%;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
  }

  pre {
    padding: 15px;
  }

  ul {
    padding-top: 15px;
    font-size: 18px;

    li {
      padding-bottom: 10px;
    }
  }

  .note {
    margin-top: 35px;
    border-left: 7px solid $theme-secondary-color;
    background-color: $gray-200;
    padding: 25px;

    h5 {
      margin-top: 0px;
    }
  }
}